import { Line, Circle } from "rc-progress";
import { Howl } from "howler";
import dayjs from "dayjs";
import isNull from "lodash/isNull";
import debounce from "lodash/debounce";

let playSound = () => {
  const howl = new Howl({ src: ["../static/sound/camera_shutter.mp3"] });
  howl.play();
};

export default class extends React.Component {
  constructor() {
    super();
    this.state = {
      percent: 0,
      blocking: false,
      lastTimeFaceShown: null
    };
    this.topUpDebounced = debounce(this.topUp, 500);
  }
  componentDidMount() {
    // setInterval(this.decrease, 1000);
    // setInterval(this.topUp, 100);
  }

  topUp = () => {
    console.log("inside topup");
    if (
      isNull(this.state.lastTimeFaceShown) ||
      dayjs().isAfter(this.state.lastTimeFaceShown.add(10, "second"))
    ) {
      console.log(1);
      this.setState({ lastTimeFaceShown: dayjs().subtract(10, "second") });
      // return;
    }
    console.log("lasttime", this.state.lastTimeFaceShown);
    console.log("now", dayjs());
    if (dayjs().isBefore(this.state.lastTimeFaceShown.add(0.5, "second"))) {
      console.log(2);
      return;
    }
    console.log(3);
    // if (this.state.blocking || !this.props.isActive()) {
    //   return;
    // }
    const percent = this.state.percent + 10;
    if (percent >= 100) {
      this.props.addToPunchList();
      playSound();
      this.block();
      this.restart();
      return;
    }
    this.setState({ percent });
  };

  decrease = () => {
    if (
      !isNull(this.state.lastTimeFaceShown) &&
      dayjs().isBefore(this.state.lastTimeFaceShown.add(10, "second"))
    ) {
      return;
    }
    if (this.state.blocking) {
      return;
    }
    const percent = this.state.percent - 1;
    if (percent <= 0) {
      return;
    }
    this.setState({ percent });
  };

  block = () => {
    this.setState({ blocking: true });
  };
  unblock = () => {
    this.setState({ blocking: false });
  };
  restart = () => {
    this.setState({ percent: 100 });
    setTimeout(() => {
      this.setState({ percent: 0 });
      this.unblock();
    }, 10 * 1000);
  };

  render() {
    return <Line strokeWidth="4" percent={this.state.percent} />;
  }
}
